export const getConfig = (additionalConfig = {}) => {
  const _config = {};
  Object.keys(additionalConfig).forEach((key) => {
    _config[key] = {
      ...(_config[key] || {}),
      ...(additionalConfig[key] || {}),
    };
  });

  return _config;
};
